<template>
	<div class="menuContainer">
		<div v-for="item in menuItem" :key="item.id" :class="item.class">
			<div><img :src="getIconPath(item.icon)" alt="" /></div>
			<div>
				{{ item.text }}
			</div>
		</div>
	</div>
</template>
<style scoped>
	.menuContainer {
		font-size: 16px;
		column-gap: 2px;
		color: white;
		display: flex;
		flex-direction: row;
	}
	.navClass {
		display: flex;
		column-gap: 4px;
		padding: 8px;
		align-items: center;
	}
	.navClass:nth-child(4) {
		margin-left: 28px;
		padding: 8px 13px;
		border: 2px solid rgba(229, 26, 74, 0.4);
		border-radius: 10px;
	}
	.navClass:nth-child(4) img {
		margin-top: -3px;
	}
	.navClass:last-child {
		padding: 8px 17px;
		flex-direction: row-reverse;
		background: rgb(229, 26, 75);
		background: linear-gradient(
			180deg,
			rgba(229, 26, 75, 1) 0%,
			rgba(71, 0, 74, 1) 100%
		);
		border-radius: 10px;
		margin-left: 25px;
	}
</style>
<script>
	import { mapGetters } from "vuex";
	export default {
		methods: {
			getIconPath(iconFileName) {
				return require(`@/assets/image/top/menuIcons/${iconFileName}`);
			},
		},
		created() {
			this.$store.dispatch("topMenu/getMenuItems");
		},
		computed: {
			...mapGetters({
				menuItem: "topMenu/_menuItems",
			}),
		},
	};
</script>
